<template>
    <section>
        <h3>진행 목록</h3>
        <ul v-for="(item,index) in items" :key="item" >
           <li class="shadow" v-if="item.state === '진행중'">
             <i class="checkBtn fa fa-check"  aria-hidden="true"></i>
                 {{item.title}}
             <span  v-if="item.state === '진행중'" class="removeBtn" type="button" @click="completeTodo(item,index)">
                완료<i class="fa fa-trash-0" aria-hidden="true"></i>
             </span>
           </li>
        </ul>
        <hr/>
        <h3>완료 목록</h3>
        <ul v-for="(item,index) in items" :key="item" >
           <li class="shadow" v-if="item.state === '완료'">
             <i class="checkBtn fa fa-check"  aria-hidden="true"></i>
                 {{item.title}}
             <span  v-if="item.state === '완료'" class="removeBtn" type="button" @click="removeTodo(item,index)">
                삭제<i class="fa fa-trash-0" aria-hidden="true"></i>
             </span>
           </li>
        </ul>
    </section>
</template>
<script>
export default {
    props:['items'],
    methods: {
       removeTodo(todoItem,index){
        // eslint-disable-next-line no-console
        console.log(todoItem);
           this.$emit("removeTodo",todoItem,index);
       },
       completeTodo(todoItem,index){
           this.$emit("completeTodo",todoItem,index);
       }
    },
}
</script>
<style>
  ul {
      list-style-type:none;
      padding-left:0px;
      margin-top:0;
      text-align : left;
  }

  li {
      display : flex;
      min-height:50px;
      height:50px;
      line-height:50px;
      margin: 0.5rem 0;
      padding:0 0.9rem;
      background:white;
      border-radius:5px;
  }

  .checkBtn{
      line-height: 45px;
      color:#62acde;
      margin-right:5px;
  }

  .removeBtn {
      margin-left : auto;
      color:#de4343;
  }

</style>