<template>
    <div class="inputBox shadow">
        <input type="text" ref="todoItem" v-model="newTodoItem" placeholder="할일을 입력하세요." @keyup.enter="addTodo">
        <button @click="addTodo">추가</button>
        <span class="addContainer" @click="addTodo">
           <i class="addBtn fa fa-plue" aria-hidden="true"></i>
        </span>
    </div>
</template>
<script>
export default {
    data(){
        return{
            newTodoItem : ""
        }
    },
    methods : {
        addTodo(){
           
           if(this.newTodoItem !== ""){
                
                //입력된 텍스트의 앞뒤 공백 문자열 제거
                let val = this.newTodoItem && this.newTodoItem.trim();

                //추가할 데이타
                let data = {
                    title : val,
                    state: "진행중"
                }

                this.$emit("addTodo",data);
                
                //인풋박스 초기화
                this.clearInput();

           }else{
               alert("해야할 목록을 입력하세요.");
           }
        },
        clearInput(){
            this.newTodoItem = "";
        }
    }
}
</script>
<style scoped>
  
   input:focus {
       outline : none;
   }

   .inputBox {
       background : white;
       height : 50px;
       line-height : 50px;
       border-radius : 5px;
   }

   .inputBox input{
       border-style:none;
       font-size:0.9rem;
   }

   .addContainer {
       float: right;
       background : linear-gradient(to right,#6478FB,#8763FB);
       display:inline-block;
       width : 3rem;
       border-radius : 0 5px 5px 0;
   }

   .addBtn{
       color : white;
       vertical-align : middle;
   }

</style>