<template>
  <div id="app">
  <!-- 등록된 컴코넌트 사용 부분 -->
   <TodoHeader/>
   <TodoInput @addTodo="addTodo"/>
   <TodoList :items="todoItems" @removeTodo="removeTodo" @completeTodo="completeTodo"/>
   <TodoFooter @removeAll="clearAll"/>
  </div>
</template>
<script>

import TodoHeader from './components/TodoHeader'  //헤더 컴포넌트 임포트
import TodoInput from './components/TodoInput'    //Todo 입력 컴포넌트 임포트
import TodoList from './components/TodoList'      //Todo 리스트 컴포넌트 임포트
import TodoFooter from './components/TodoFooter'  //풋터 컴토넌트 임포트

export default {
  //데이터 속성 todoItems 선언
  data(){
    return {
      todoItems: []
    }
  },
  methods : {

      //할일목록을 추가
      addTodo(todoitem){

        if(localStorage.getItem(todoitem.title) != null){
          alert("같은 항목이 존재합니다.");
          return;
        }
       
      localStorage.setItem(todoitem.title,todoitem.state);
      this.todoItems.push(todoitem);
      
     },

     //할일목록 전체를 삭제
     clearAll(){

      for(let i=0;i<this.todoItems.length;i++) {

        if(this.todoItems[i].state === "완료"){
            this.todoItems.splice(i,1);
        }
      }

      for(let i=0;i<localStorage.length;i++) {

         if(localStorage.getItem(localStorage.key(i)) === "완료"){
            localStorage.removeItem(localStorage.key(i))
         }

      }

       //this.todoItems=[];
       alert("all todo item clear")
     },
     
     //해당 할일을 삭제
     removeTodo(todoItem,index){
      localStorage.removeItem(todoItem.title);
      this.todoItems.splice(index,1);
    },
    completeTodo(todoItem,index){

      // eslint-disable-next-line no-console
      console.log(index)

      localStorage.setItem(todoItem.title,"완료");
      this.todoItems.find(e => e.title == todoItem.title).state ="완료"

    }

  },
  //임포트한 컴포넌트를 template 에서 사용하기 위해 컴포넌트 등록
   components : {
     TodoHeader,
     TodoInput,
     TodoList,
     TodoFooter
   },
   created(){

        if(localStorage.length >0){
            for(var i=0;i<localStorage.length;i++){
                this.todoItems.push({title:localStorage.key(i),state:localStorage.getItem(localStorage.key(i))});
            }
        }
    }
}
</script>
<style>
  body {
      text-align:center;
      background-color : #F6F6F8
  }

  input {
      border-style : groove;
      width : 200px;
  }

  button {
  border-style:groove;
  }

  .show{
      box-shadow: 5px 10px 10px rgba(0,0,0,0.3)
  }
  
</style>